#first-page {
    padding-top: 50px
}
#first-page .form-wrap {
    width: 30%;
    margin: 0 auto;
}
#first-page h1 {
    color: #1fa67b;
    font-size: 18px;
    text-align: center;
    font-weight: bold;
    padding-bottom: 20px;
}
#first-page .form-group {
    margin-bottom: 25px;
}
#first-page .btn.btn-custom {
    font-size: 14px;
    margin-bottom: 20px;
}

@media (max-width: 1000px) {
    #first-page .form-wrap {
        width: 80%;
        margin: 0 auto;
    }
}

/*    --------------------------------------------------
	:: Inputs & Buttons
	-------------------------------------------------- */
.form-control {
    color: #212121;
}
.btn-custom {
    color: #fff;
    background-color: #1fa67b;
}
.btn-custom:hover,
.btn-custom:focus {
    color: #fff;
}


